import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import popupMessage from "@/components/popupMessage.vue"
let _this,timer;
export default {
	components:{
		uniLoadMore,
		popupMessage
	},
	data() {
		return {
			applyCode:'',
			record:[],
			loadingType: 'more',
			pageNum: 1,
			pageSize: 20,
			content:''
			
		};
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchList();
		        }, 1000);
	},
	onLoad:function(){
		_this = this
		this.fetchList()
		
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchMoreList();
		        }, 1000);
	},
	methods:{
		showDialog:function(e){
			this.applyCode = e
			this.content = `確認要取消該單嗎？`
			this.$refs.dialog.show()
		},
		confirmInput:function(type){
			this.cancelOrder()
		
		},
		showPicture:function(src){
			uni.previewImage({
				urls: [src],
				success:function(){
					console.log('正在预览中')
				}
				
			})
		},
		handleDate:function(d){
			let arr = d.split(' ')
			let date1 = arr[0].split('-')
			let date2 = arr[1].split(':')
			
			return date1[1]+'-'+date1[2]+' '+date2[0]+':'+date2[1]
		},
		async fetchList(){
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize
				
			}
			const res = await this.$http.get('/topup/apply_list',{data: data})
			this.record = res.data.data.list
			this.record.forEach((item)=>{
				item.createTime = this.handleDate(item.createTime)
				
			})
			
			// this.filterList(res.data.data.list)
			// this.zijinlistback(res.data.data.list)
			// this.record = this.zijinList
			console.log(res)
		},
        async cancelOrder(){
			let data ={
				applyCode: this.applyCode
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			})
			const res = await this.$http.get('/topup/apply_del',{data: data})
			uni.hideLoading()
			uni.showToast({
				title: '已取消',
				duration: 1500
			});
			
			this.fetchList()
		}
		
	}
}
